.wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
}
.counterCard{
    text-align: center;
    border: 3px solid black;
    margin-top: 10%;
    padding: 2%;
}
.incrementBtn{
    background-color: green;
    border: none;
    height: 30px
}
.decrementBtn{
    background-color: rgb(128, 0, 0);
    border: none;
    height: 30px
}
.resetBtn{
    background-color: rgb(40, 181, 232);
    border: none;
    height: 30px
}